	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-11-08-dr-hyun-oh-yoo-cyworld-2/index.md absPath of file >>> MarkdownRemark","html":"<p>Korean social site. Combination of popular internet activities – blogs, photo, video sharing.\nHere are some statistics:\n20 million subscribers\n40% of population\n96% of 20-29 year olds use regularly\n20 billion page views per month</p>\n<p>They are coming to the Unites States. It will be interesting to see if Cyworld is successful. Their design and timing fit well with the Korean market, but many of the components already have successful counterparts in the US – flickr, youtube, myspace etc. In addition, the design is very Asian which may or may not resonate in the American market. Culture is important to web design and simply translating content is proving to be insufficient for success. I imagine that is why Jack Ma is successful in China where US companies have failed. It also makes me wonder if there is application for open content.</p>","frontmatter":{"title":"Dr Hyun-Oh Yoo Cyworld","date":"November 08, 2006","imageUrl":null,"image":null}}},"pathContext":{"path":"/dr-hyun-oh-yoo-cyworld-2"}}

